export const demoLibrary = [{"title":"Программист-фанатик","author":"Чед Фаулер","yearIssue":"2009","genre":"Schoolbook","cover":"https://monster-book.com/sites/default/files/styles/sc290x400/public/books/programmist-fanatik.png?itok=6q1YIkzx","text":"В этой книге вы не найдете описания конкретных технологий, алгоритмов\nи языков программирования – ценность ее не в этом. Она представляет\nсобой сборник практических советов и рекомендаций, касающихся\nситуаций, с которыми порой сталкивается любой разработчик: отсутствие\nмотивации, выбор приоритетов, психология программирования, отношения\nс руководством и коллегами и многих других. Подобные знания обычно\nприходят лишь в результате многолетнего опыта реальной работы. По\nбольшому счёту перед вами – ярко и увлекательно написанное руководство,\nкоторое поможет быстро сделать карьеру в индустрии разработки ПО любому,\nкто поставил себе такую цель. Конечно, опытные программисты могут найти\nнекоторые идеи автора достаточно очевидными, но и для таких найдутся\nтемы, которые позволят пересмотреть устоявшиеся взгляды и выйти на новый\nуровень мастерства. Для тех же, кто только в самом начале своего пути как\nразработчика, чтение данной книги, несомненно, откроет широчайшие\nперспективы.","id":1633101314941,"pdf":"https://donski.live/pdfBooks/12431910a4.pdf"},{"title":"Идеальный программист","author":"Роберт Мартин","yearIssue":"2011","genre":"Schoolbook","cover":"https://monster-book.com/sites/default/files/styles/sc290x400/public/books/idealnyy-programmist.jpg?itok=ponxEaDm","text":"Всех программистов, которые добиваются успеха в мире разработки ПО,\nотличает один общий признак: они больше всего заботятся о качестве\nсоздаваемого программного обеспечения. Это – основа для них. Потому\nчто они являются профессионалами своего дела. В этой книге легендарный\nэксперт Роберт Мартин (более известный в сообществе как «Дядюшка Боб»),\nавтор бестселлера «Чистый код», рассказывает о том, что значит «быть\nпрофессиональным программистом», описывая методы, инструменты и\nподходы для разработки «идеального ПО». Книга насыщена практическими\nсоветами в отношении всех аспектов программирования: от оценки проекта\nи написания кода до рефакторинга и тестирования. Эта книга – больше, чем\nописание методов, она о профессиональном подходе к процессу разработки. В\nформате ios.epub представлен издательский файл.","id":1633103715320,"pdf":"https://donski.live/pdfBooks/80750542a4.pdf"},{"title":"Код. Тайный язык","author":"Чарльз Петцольд","yearIssue":"2000","genre":"Schoolbook","cover":"https://monster-book.com/sites/default/files/styles/sc290x400/public/books/kod-taynyy-yazyk-informatiki_0.png?itok=kc8ExhSO","text":"Книга «Код» представляет собой увлекательное путешествие в прошлое – мир\nэлектрических устройств и телеграфных машин. Знакомство с прообразами\nпервых компьютеров позволит читателю с любым уровнем технической\nподготовки узнать о том, как работают современные электронные устройства.","id":1633104006824,"pdf":"https://donski.live/pdfBooks/56367900a4.pdf"},{"title":"Совершенный КОД","author":"Стив Макконнелл","yearIssue":"2017","genre":"Schoolbook","cover":"https://monster-book.com/sites/default/files/styles/sc290x400/public/books/sovershennyy-kod.jpg?itok=91Cf3Ohw","text":"Более 10 лет первое издание этой книги считалось одним из лучших практических руководств по программированию. Сейчас эта книга полностью обновлена с учетом современных тенденций и технологий и дополнена сотнями\nновых примеров, иллюстрирующих искусство и науку программирования. Опираясь на академические исследования, с одной стороны, и практический опыт\nкоммерческих разработок ПО — с другой, автор синтезировал из самых эффективных методик и наиболее эффективных принципов ясное прагматичное руководство. Каков бы ни был ваш профессиональный уровень, с какими бы средствами разработки вы ни работали, какова бы ни была сложность вашего проекта,\nв этой книге вы найдете нужную информацию, она заставит вас размышлять\nи поможет создать совершенный код.\nКнига состоит из 35 глав, предметного указателя и библиографии.","id":1633104214110,"pdf":"https://donski.live/pdfBooks/sovershennyykod.pdf"}]



